.product {
    display:flex;
    flex-direction:column;
    align-items: center;
    justify-content: flex-end;
    margin:10px;
    padding:20px;
    width:100%;
    max-height:400px;
    min-width:100px;
    background-color:white;
    z-index:1;
}
 
.product__rating{
    display:flex;
}
.product>img{
    max-height:200px;
    width:100%;
    object-fit:contain;
    margin-bottom:15px;
}


.product>button{
    background:rgba(238, 255, 0, 0.938);
    border: 1px solid;
    margin-top:10px;
    border-color:white;
    color:#111;
}


.product__price{
    margin-top:5px;
}

.product__info{
    height:100%;
    margin-bottom:15px;
}