.home{
    display:flex;
    justify-content:center;
    margin-left:auto;
    margin-right:auto;
    max-width:1500px;
}

.home__row{
    display:flex;
    z-index:1;
    margin-left:5px;
    margin-right:5px;
}


.home__image{
    width:95%;
    z-index:-1;
    margin-bottom:-130px;
    mask-image: linear-gradient(to bottom, rgba(0,0,0,1));
}

